
.grid {
    display: grid;
    width: 100%;
    margin: 0 auto;
    grid-template-areas:
    "header header header"
    "nav content content"
    "footer footer footer";
    grid-template-rows: 60px auto 100px;
    grid-template-columns: 1fr 3fr 3fr;
    grid-gap: 5px;
}










